.instructions {
	width: 100%;
	height: 100vh;
    z-index: 1;
    position: absolute;
    background: #1e2027bf;
    color: white;
    padding-top: 20vh;
    font-weight: bolder;
    text-align: center;
}

.btn {
    background-color: #1f7cd8
}

.btn:hover {
    -webkit-transform: scale(1.2) ;
        -ms-transform: scale(1.2) ;
            transform: scale(1.2) ;
    background-color: hsl(192, 60%, 58%)
}


